// Systme de versioning automatique - Semantic Versioning (semver)
// Format: MAJOR.MINOR.PATCH
// MAJOR: Changements incompatibles avec versions prcdentes
// MINOR: Nouvelles fonctionnalits compatibles
// PATCH: Corrections de bugs compatibles

export const VERSION_CONFIG = {
  major: 1,      // Version majeure - changements breaking
  minor: 16,      // Version mineure - nouvelles fonctionnalits
  patch: 0,      // Version patch - corrections de bugs
  buildDate: "2025-07-28", // Date de build automatique
};

// Gnration automatique du numro de version
export const getVersion = () => {
  const { major, minor, patch } = VERSION_CONFIG;
  return `${major}.${minor}.${patch}`;
};

// Gnration de l'anne courante
export const getCurrentYear = () => {
  return new Date().getFullYear();
};

// Information complte de version
export const getVersionInfo = () => {
  return {
    version: getVersion(),
    buildDate: VERSION_CONFIG.buildDate,
    year: getCurrentYear()
  };
};

